<template>
  <div class="hello">
    <div style="display: flex; justify-content: center;">
      <div class="label"></div>
      <div class="nowjieci"><p v-html="getJieCi"></p></div>
    </div>
    <div style="display: flex; justify-content: center;">
      <div class="nowjieci"><p v-html="getJieCiFenBu"></p></div>
    </div>
    <div style="height: 10px;"></div>
    <div class="nowtime"><p v-html="formattedTime"></p></div>
    <div class="nowtime"><p v-html="lunarDate"></p></div>
  </div>
</template>

<script>
import { getLunar } from 'chinese-lunar-calendar';

export default {
  name: 'CurrentDateTime',
  data() {
    return {
      timestamp: Date.now(), // 初始时间戳（毫秒）
      weekList: ['星期日', '星期一', '星期二', '星期三', '星期四', '星期五', '星期六'],
      weekShortList: ['日', '一', '二', '三', '四', '五', '六']  
    }
  },
  methods: {
    parseTime(timeStr) {
      return {
        hours: parseInt(timeStr.substring(0, 2), 10),
        minutes: parseInt(timeStr.substring(2, 4), 10),
        seconds: parseInt(timeStr.substring(4, 6), 10)
      };
    },
    toTotalSeconds(time) {
      return time.hours * 3600 + time.minutes * 60 + time.seconds;
    },
    
    formatTime(timeStr) {
      const time = this.parseTime(timeStr);
      return `${this.padZero(time.hours)}:${this.padZero(time.minutes)}:${this.padZero(time.seconds)}`;
    },
    padZero(num) {
      return num.toString().padStart(2, "0");
    },
    calculateTimeDiff(startTimeStr, endTimeStr) {
      // 解析两个时间
      const start = this.parseTime(startTimeStr);
      const end = this.parseTime(endTimeStr);

      // 转换为总秒数
      const startSeconds = this.toTotalSeconds(start);
      const endSeconds = this.toTotalSeconds(end);

      // 计算差值（处理跨天情况：如果目标时间早于起始时间，加24小时的秒数）
      let diffSeconds = endSeconds - startSeconds;
      if (diffSeconds < 0) {
        diffSeconds += 24 * 3600; // 24小时 = 86400秒
      }

      // 转换差值为小时、分钟、秒
      const hours = Math.floor(diffSeconds / 3600);
      diffSeconds %= 3600; // 剩余秒数
      const minutes = Math.floor(diffSeconds / 60);
      const seconds = diffSeconds % 60;

      // 保存结果
      return this.padZero(hours)+"时"+this.padZero(minutes)+"分"+this.padZero(seconds)+"秒";
    },
  },
  computed: {

    lunarDate() {
      const today = new Date(this.timestamp)
      const lunar = getLunar(today.getFullYear(), today.getMonth()+1, today.getDate())
      const lunaryearstr = lunar.lunarYear
      const lunardatestr = lunar.dateStr
      return `${lunaryearstr} ${lunardatestr}`
    },
    // 格式化为：2024年06月15日16时22分08秒
    formattedTime() {
      const date = new Date(this.timestamp)
      const year = date.getFullYear()
      const month = String(date.getMonth() + 1).padStart(2, '0') // 01~12
      const day = String(date.getDate()).padStart(2, '0')         // 01~31
      const hours = String(date.getHours()).padStart(2, '0')      // 00~23
      const minutes = String(date.getMinutes()).padStart(2, '0')  // 00~59
      const seconds = String(date.getSeconds()).padStart(2, '0')  // 00~59
      const weekday = this.weekList[date.getDay()]
      return `${year} 年 ${month} 月 ${day} 日<br/>${hours} 时 ${minutes} 分 ${seconds} 秒<br/>${weekday}`
    },
    // 转换为秒级时间戳
    timestampInSeconds() {
      return Math.floor(this.timestamp / 1000)
    },
    getJieCiFenBu() {
      const date = new Date(this.timestamp)
      // const year = date.getFullYear()
      const month = String(date.getMonth() + 1).padStart(2, '0') // 01~12
      // const day = String(date.getDate()).padStart(2, '0')         // 01~31
      const hours = String(date.getHours()).padStart(2, '0')      // 00~23
      const minutes = String(date.getMinutes()).padStart(2, '0')  // 00~59
      const seconds = String(date.getSeconds()).padStart(2, '0')  // 00~59
      const timestr = hours + minutes + seconds
      const shortweekday = this.weekShortList[date.getDay()]
      if (timestr >= "063000" && timestr < "080000"){
        return "当前无课时"
      } else if (timestr >= "080000" && timestr < "084000"){
        return "当前课时分布：" + "<a target='_blank' href='https://xinxipingtai.github.io/"+shortweekday+"_1.html'>"+shortweekday+"_1</a>"
      } else if(timestr >= "084000" && timestr < "085500"){
        return "当前无课时"
      } else if(timestr >= "085500" && timestr < "093500"){
        return "当前课时分布：" + "<a target='_blank'  href='https://xinxipingtai.github.io/"+shortweekday+"_2.html'>"+shortweekday+"_2</a>"
      } else if(timestr >= "093500" && timestr < "100000"){
        return "当前无课时"
      } else if(timestr >= "100000" && timestr < "104000"){
        return "当前课时分布：" + "<a target='_blank'  href='https://xinxipingtai.github.io/"+shortweekday+"_3.html'>"+shortweekday+"_3</a>"
      } else if(timestr >= "104000" && timestr < "105500"){
        return "当前无课时"
      } else if(timestr >= "105500" && timestr < "113500"){
        return "当前课时分布：" + "<a  target='_blank' href='https://xinxipingtai.github.io/"+shortweekday+"_4.html'>"+shortweekday+"_4</a>"
      } 
      if (month == '05' || month == '06' || month == '07' || month == '08' || month == '09'){
        // 夏季时间
        if (timestr >= "113500" && timestr < "143000"){
          return  "当前无课时"
        } else if (timestr >= "143000" && timestr < "151000"){
          return "当前课时分布：" + "<a  target='_blank' ref='https://xinxipingtai.github.io/"+shortweekday+"_5.html'>"+shortweekday+"_5</a>"
        } else if(timestr >= "151000" && timestr < "152500"){
          return  "当前无课时"
        } else if(timestr >= "152500" && timestr < "160500"){
          return "当前课时分布：" + "<a  target='_blank' href='https://xinxipingtai.github.io/"+shortweekday+"_6.html'>"+shortweekday+"_6</a>"
        } else if(timestr >= "160500" && timestr < "162000"){
          return  "当前无课时"
        }  else if(timestr >= "162000" && timestr < "170000"){
          return "当前课时分布：" + "<a  target='_blank' href='https://xinxipingtai.github.io/"+shortweekday+"_7.html'>"+shortweekday+"_7</a>"
        }  else if(timestr >= "170000" && timestr < "193000"){
          return  "当前无课时"
        } else if(timestr >= "193000" && timestr < "201000"){
          return  "当前无课时"
        }  else if(timestr >= "201000" && timestr < "202500"){
          return  "当前无课时"
        }  else if(timestr >= "202500" && timestr < "210500"){
          return  "当前无课时"
        } 
      }else if(month == '10' || month == '11' || month == '12' || month == '01' || month == '02' || month == '03' || month == '04'){
        // 冬季时间
        if (timestr >= "113500" && timestr < "140000"){
          return  "当前无课时"
        } else if (timestr >= "140000" && timestr <= "144000"){
          return "当前课时分布：" + "<a  target='_blank' href='https://xinxipingtai.github.io/"+shortweekday+"_5.html'>"+shortweekday+"_5</a>"
        } else if(timestr >= "144000" && timestr < "145500"){
          return  "当前无课时"
        } else if(timestr >= "145500" && timestr <= "153500"){
          return "当前课时分布：" + "<a  target='_blank' href='https://xinxipingtai.github.io/"+shortweekday+"_6.html'>"+shortweekday+"_6</a>"
        } else if(timestr >= "153500" && timestr < "155000"){
          return  "当前无课时"
        }  else if(timestr >= "155000" && timestr <= "163000"){
          return "当前课时分布：" + "<a  target='_blank' href='https://xinxipingtai.github.io/"+shortweekday+"_7.html'>"+shortweekday+"_7</a>"
        } else if(timestr >= "163000" && timestr <= "190000"){
          return  "当前无课时"
        } else if(timestr >= "190000" && timestr <= "194000"){
          return  "当前无课时"
        }  else if(timestr >= "194000" && timestr < "195500"){
          return  "当前无课时"
        }  else if(timestr >= "195500" && timestr <= "203500"){
          return  "当前无课时"
        } 
      }
      return "当前无课时"
    },
    getJieCi() {
      const date = new Date(this.timestamp)
      // const year = date.getFullYear()
      const month = String(date.getMonth() + 1).padStart(2, '0') // 01~12
      // const day = String(date.getDate()).padStart(2, '0')         // 01~31
      const hours = String(date.getHours()).padStart(2, '0')      // 00~23
      const minutes = String(date.getMinutes()).padStart(2, '0')  // 00~59
      const seconds = String(date.getSeconds()).padStart(2, '0')  // 00~59
      const timestr = hours + minutes + seconds
      if (timestr >= "063000" && timestr < "080000"){
        return "早餐" + "<br/>【距离上课还有 " + this.calculateTimeDiff(timestr, "080000") + "】"
      } else if (timestr >= "080000" && timestr < "084000"){
        return "第一节" + "<br/>【距离下课还有 " + this.calculateTimeDiff(timestr, "084000") + "】"
      } else if(timestr >= "084000" && timestr < "085500"){
        return "课间" + "<br/>【距离上课还有 " + this.calculateTimeDiff(timestr, "085500") + "】"
      } else if(timestr >= "085500" && timestr < "093500"){
        return "第二节" + "<br/>【距离下课还有 " + this.calculateTimeDiff(timestr, "093500") + "】"
      } else if(timestr >= "093500" && timestr < "100000"){
        return "大课间" + "<br/>【距离上课还有 " + this.calculateTimeDiff(timestr, "100000") + "】"
      } else if(timestr >= "100000" && timestr < "104000"){
        return "第三节" + "<br/>【距离下课还有 " + this.calculateTimeDiff(timestr, "104000") + "】"
      } else if(timestr >= "104000" && timestr < "105500"){
        return "课间" + " <br/>【距离上课还有 " + this.calculateTimeDiff(timestr, "105500") + "】"
      } else if(timestr >= "105500" && timestr < "113500"){
        return "第四节" + " <br/>【距离下课还有 " + this.calculateTimeDiff(timestr, "113500") + "】"
      } 
      if (month == '05' || month == '06' || month == '07' || month == '08' || month == '09'){
        // 夏季时间
        if (timestr >= "113500" && timestr < "143000"){
          return "午餐" + "<br/>【距离上课还有 " + this.calculateTimeDiff(timestr, "143000") + "】"
        } else if (timestr >= "143000" && timestr < "151000"){
          return "第五节" + "<br/>【距离下课还有 " + this.calculateTimeDiff(timestr, "151000") + "】"
        } else if(timestr >= "151000" && timestr < "152500"){
          return "课间" + "<br/>【距离上课还有 " + this.calculateTimeDiff(timestr, "152500") + "】"
        } else if(timestr >= "152500" && timestr < "160500"){
          return "第六节" + "<br/>【距离下课还有 " + this.calculateTimeDiff(timestr, "160500") + "】"
        } else if(timestr >= "160500" && timestr < "162000"){
          return "课间" + "<br/>【距离上课还有 " + this.calculateTimeDiff(timestr, "162000") + "】"
        }  else if(timestr >= "162000" && timestr < "170000"){
          return "第七节" + "<br/>【距离下课还有 " + this.calculateTimeDiff(timestr, "170000") + "】"
        }  else if(timestr >= "170000" && timestr < "193000"){
          return "晚饭" + "<br/>【距离上课还有 " + this.calculateTimeDiff(timestr, "193000") + "】"
        } else if(timestr >= "193000" && timestr < "201000"){
          return "晚自习第一节" + "<br/>【距离下课还有 " + this.calculateTimeDiff(timestr, "201000") + "】"
        }  else if(timestr >= "201000" && timestr < "202500"){
          return "课间" + "<br/>【距离上课还有 " + this.calculateTimeDiff(timestr, "202500") + "】"
        }  else if(timestr >= "202500" && timestr < "210500"){
          return "晚自习第二节" + "<br/>【距离下课还有 " + this.calculateTimeDiff(timestr, "210500") + "】"
        } 
      }else if(month == '10' || month == '11' || month == '12' || month == '01' || month == '02' || month == '03' || month == '04'){
        // 冬季时间
        if (timestr >= "113500" && timestr < "140000"){
          return "午餐" + "<br/>【距离上课还有 " + this.calculateTimeDiff(timestr, "140000") + "】"
        } else if (timestr >= "140000" && timestr <= "144000"){
          return "第五节" + "<br/>【距离下课还有 " + this.calculateTimeDiff(timestr, "144000") + "】"
        } else if(timestr >= "144000" && timestr < "145500"){
          return "课间" + "<br/>【距离上课还有 " + this.calculateTimeDiff(timestr, "145500") + "】"
        } else if(timestr >= "145500" && timestr <= "153500"){
          return "第六节" + "<br/>【距离下课还有 " + this.calculateTimeDiff(timestr, "153500") + "】"
        } else if(timestr >= "153500" && timestr < "155000"){
          return "课间" + "<br/>【距离上课还有 " + this.calculateTimeDiff(timestr, "155000") + "】"
        }  else if(timestr >= "155000" && timestr <= "163000"){
          return "第七节" + "<br/>【距离下课还有 " + this.calculateTimeDiff(timestr, "163000") + "】"
        } else if(timestr >= "163000" && timestr <= "190000"){
          return "晚饭" + "<br/>【距离上课还有 " + this.calculateTimeDiff(timestr, "190000") + "】"
        } else if(timestr >= "190000" && timestr <= "194000"){
          return "晚自习第一节" + "<br/>【距离下课还有 " + this.calculateTimeDiff(timestr, "194000") + "】"
        }  else if(timestr >= "194000" && timestr < "195500"){
          return "课间" + "<br/>【距离上课还有 " + this.calculateTimeDiff(timestr, "195500") + "】"
        }  else if(timestr >= "195500" && timestr <= "203500"){
          return "晚自习第二节" + "<br/>【距离下课还有 " + this.calculateTimeDiff(timestr, "203500") + "】"
        } 
      }
      return "休息时间"
    }
  },
  mounted() {
    // 每秒更新一次时间戳
    this.interval = setInterval(() => {
      this.timestamp = Date.now()
    }, 1000)
  },
  beforeUnmount() {
    // 组件销毁前清除定时器，避免内存泄漏
    clearInterval(this.interval)
  }
}
</script>

